body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  font-family: 'Noto Sans JP', sans-serif
}

#root {
  width: 100%;
  height: 100%;
}

.App {
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, #4682B4, #87CEEB);
  position: relative;

}

#menu {
  height: 10%;
  position: absolute;
  top: 0;
  width: 98%;
}

.open-menu {
  color: black;
  background-color: rgba(211, 211, 211, 0.5);
  margin: 1% 1% 0 1%;
  border-radius: 15px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  max-width: 98vw;
}

#button-set {
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  align-items: center;
}

.close-menu {
  margin: 1% 1% 1% 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  padding-left: 20px;
}

.menu-button {
  padding: 5px 10px 5px 10px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  text-decoration: none;
  outline: none;
}

#open-button {
  filter: invert(100);
}

#close-button {
}

.view-button {
  color: black;
  background-color: rgba(0, 0, 0, 0);
  text-decoration: none;
  border: none;
  padding: 5px 10px 5px 10px;
  outline: none;
  font-family: inherit;
  font-weight: bold;
}

.current {
  color: white;
  background-color: dimgray;
  border-radius: 5px;
}

#translator {
  position: absolute;
  top: 15%;
  height: 85%;
  width: 100%;
}

#editor,
#preview {
  background-color: dimgray;
  color: white;
  border: none;
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.2);
  outline: none;
  overflow-y: scroll;
  scrollbar-width: thin;
  scrollbar-color: #d3d3d3 grey;
  padding: 10px 20px;
}

#editor::-webkit-scrollbar, #preview::-webkit-scrollbar {
  width: 10px;
}

#editor::-webkit-scrollbar-track, #preview::-webkit-scrollbar-track {
  background: grey;
}

#editor::-webkit-scrollbar-thumb, #preview::-webkit-scrollbar-thumb {
  background: #d3d3d3;
  border-radius: 10px;
}


#editor {
  resize: none;
}

#preview {
  font-family: inherit;
}

#preview img {
  max-width: 10%;
  display: block;
}

.side-by-side-container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: stretch;
}

.side-by-side-item {
  height: 90%;
  width: 45%;
}

.top-bottom-container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-evenly;
  align-items: center;
}

.top-bottom-item {
  width: 90%;
  height: 40%;
}

.all-editor-container,
.all-preview-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}

.all-editor-item,
.all-preview-item {
  width: 90%;
  height: 90%;
  margin-bottom: 2%;
}